.title {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.title_bar button {
  width: 40px;
  height: 40px;
  display: flex;
  padding: 3px;
  cursor: pointer;
  font-size: 30px;
  border: none;
  background-color: tomato;
  color: white;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
}
